import React, { useState } from "react";
import Header from "./Header";
import Navigationbar from "./Navigationbar";
import Footer from "./Footer";
import Input from "./Input";
import axios from "axios";
import Cookies from "js-cookie";

function Security(props) {
  const toggle = props.toggle;
  const mode = props.mode;
  const [loading, setLoading] = useState(false);
  const [currentpassword, setCurrentassword] = useState("");
  const [newpassword, setNewpassword] = useState("");
  const [confirmpassword, setConfirmpassword] = useState("");
  const changePassword = async () => {
    try {
      if (!newpassword || !currentpassword || !confirmpassword) {
        alert("fields are empty");
        return;
      }
      if (newpassword !== confirmpassword) {
        alert("New password and confirm password is smae");
        return;
      }
      setLoading(true);
      const response = await axios.put(
        "https://apis.mailmort.co/users",
        {
          password: currentpassword,
          new_password: newpassword,
        },
        {
          headers: { Authorization: "Bearer " + Cookies.get("token") },
        }
      );
      alert("updated");
    } catch (error) {
      alert("wrong  password");
    } finally {
      setLoading(false);
    }
  };
  return (
    <div id="page-container" className={mode}>
      <Navigationbar onClickHandler={toggle} />
      <Header />
      {loading && (
        <div className="relative">
          <div
            className="spinner-border fixed bg-white z-[100] ml-[35%] mt-[25%]"
            role="status"
          ></div>
          <div className="fixed top-0 left-0 w-full h-full bg-transparent z-[101]"></div>
        </div>
      )}
      <main id="main-container" className={`${loading ? "blur-md" : ""}`}>
        <div className="bg-body-light">
          <div className="content content-full">
            <div className="d-flex flex-column flex-sm-row justify-content-sm-between align-items-sm-center py-2">
              <div className="flex-grow-1">
                <h1 className="h3 fw-bold mb-2">Security Settings</h1>
                <h2 className="fs-base lh-base fw-medium text-muted mb-0">
                  Authentication credentials and setting for your account.
                </h2>
              </div>
              <nav
                className="flex-shrink-0 mt-3 mt-sm-0 ms-sm-3"
                aria-label="breadcrumb"
              >
                <ol className="breadcrumb breadcrumb-alt">
                  <li className="breadcrumb-item">
                    <a className="link-fx" href="#">
                      Security
                    </a>
                  </li>
                  <li className="breadcrumb-item" aria-current="page">
                    Settings
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>

        <div className="content">
          <div className="block block-rounded">
            <div className="block-header block-header-default">
              <h3 className="block-title">Change Password</h3>
            </div>
            <div className="block-content block-content-full space-y-3">
              <div className="mb-5 ">
                <Input
                  name="Current Password"
                  type="text"
                  value={currentpassword}
                  set={setCurrentassword}
                  placeholder="Current Password"
                />
              </div>
              <div className="mb-5">
                <Input
                  name="New Password"
                  type="password"
                  value={newpassword}
                  set={setNewpassword}
                  placeholder="New Password"
                />
              </div>
              <div className="mb-5">
                <Input
                  name="Confirm Password"
                  type="password"
                  value={confirmpassword}
                  set={setConfirmpassword}
                  placeholder="Confirm Password"
                />
              </div>
              <button className="btn btn-secondary" onClick={changePassword}>
                Update
              </button>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}

export default Security;
